::selection {
	background-color: $accent;
	color: #fff;
	text-shadow: none; }

//::placeholder
//	//font-family: $add-font
//	letter-spacing: -0.27px


body,
html {
	height: 100%;
	margin: 0; }
body {
	font: 400 14px/1.3333 $font-defoult;
	color: $dark;

	* {
		box-sizing: border-box;
		outline: none;
		&:hover,
		&:focus {
			outline: none; } }
	&.modal-open {
		overflow: hidden; } }
a {
	text-decoration: none;
	color: $dark;
	@include transition(all, .4s, ease);

	&:hover,
	&:focus {
		text-decoration: none; } }

img {
	max-width: 100%;
	vertical-align: middle; }

h1, h2, h3, h4, h5, h6 {
	font-family: $font-defoult;
	font-weight: 500;
	margin-top: 0;
	margin-bottom: 20px; }

h6 {
	margin-bottom: 12px; }

.h1 {
	font-size: 34px; }
.h2 {
	font-size: 26px; }
.h3 {
	font-size: 20px; }
.h4 {
	font-size: 18px; }
.h5, .h6 {
	font-size: 18px; }
p {
	color: $color-font;
	margin-top: 0;
	margin-bottom: 15px; }

button, input, textarea, .e-input {
	@include transition(all, .4s, ease); }


input, textarea, .e-input {
	display: block;
	border: 2px solid transparent;
	//background-color: $grey-light
	padding: 13px 11px 11px;
	width: 100%;
	font-size: 16px;
	&.error, &:focus:required:invalid {
 }		//border-color: $input-error-border
	&:focus,
	&:required:valid {
		border-color: $accent; } }

input[type="time"]::-webkit-outer-spin-button,
input[type="time"]::-webkit-inner-spin-button,
input[type="time"]::-webkit-inner-spin-button {

	-webkit-appearance: none;
	-moz-appearance: textfield;
	margin: 0;
	padding: 0;
	width: 0;
	display: none; }

button {
	font-size: 1em;
	padding: 0;
	background-color: transparent;
	border: none;
	box-shadow: none; }
video {
	max-width: 100%; }
table {
	width: 100%; }

ul {
	list-style: none;
	padding: 0;
	margin: 0; }

figure {
	margin: 0; }


@media screen and (max-width: $max-lg) {
	body {
		&.menu-open {
			overflow: hidden; } } }
@media screen and (min-width: $lg) {
	//body
	//	font-size: 20px

	.h1 {
		font-size: 64px; }
	.h2 {
		font-size: 48px; }
	.h3 {
		font-size: 28px; }
	.h4 {
		font-size: 24px; }
	.h5 {
		font-size: 20px; } }

