.b-coupon {
	width: 100%;
	background-color: #ffffff;
	padding: 6px;
	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 4px 4px;
		margin-bottom: 6px;
		border-bottom: 1px solid #dde0e9; }

	&__price {
		font-size: 20px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 0.57px;
		color: $addition-color;
		padding-right: 8px; }


	&__store {
		display: flex;
		align-items: center;

		font-size: 12px;
		letter-spacing: 0.26px;
		color: $color-silver; }

	&__store-icon {
		font-size: 18px;
		margin-right: 10px; }

	&__body {
		display: flex; }

	&__picture {
		flex: 0 0 38%;
		max-width: 38%; }

	&__desc {
		display: flex;
		align-items: flex-start;
		flex-direction: column;
		justify-content: space-between;
		padding-left: 6px;
		padding-right: 3px; }

	&__caption {
		font-size: 12px;
		font-weight: 500;
		line-height: 1.37;
		margin-bottom: 2px; }

	&__category {
		position: relative;
		font-weight: 300;
		font-style: italic;

		&:before {
			content: '|';
			display: inline-block;
			margin: 0 5px;
			font-style: normal; } }

	&__order-from {
		font-size: 12px;
		color: $color-silver;
		margin-bottom: 8px; }
	&__desc-footer {
		display: flex;
		align-items: center;
		width: 100%;
		justify-content: space-between; }

	&__date {
		font-size: 11px;
		color: $color-silver;
		margin-right: 3px; }
	&__btn {
		font-size: 12px;
		padding-left: 17px;
		padding-right: 17px; }

	//.b-coupon
	@media screen and (min-width: $xs) {
		&__desc-footer {
			margin-bottom: 25px; } }
	@media screen and (min-width: $sm) {
		&__caption {
			font-size: 14px;
			margin-bottom: 4px; }

		&__picture {
			flex: 0 0 35.5%;
			max-width: 35.5%; }
		&__desc {
			padding-left: 3.5%;
			padding-right: 1%; }
		&__header {
			padding: 0 8px 8px;
			margin-bottom: 8px; }
		&__order-from {
			font-size: 14px; }
		&__date {
			font-size: 12px;
			margin-right: 8px; } }

	@media screen and (min-width: $lg) {
		&__caption {
			font-size: 16px;
			margin-bottom: 8px; }
		&__price {
			font-size: 26px; }
		&__picture {
			flex: 0 0 38%;
			max-width: 38%; }
		&__store {
			padding-right: 20px; }
		&__desc {
			padding-left: 5.5%;
			padding-right: 3%; }
		&__btn {
			font-size: 14px; } } }
