.b-social {
	&__item {

		margin: 0 7px 0 0;
		.e-icon {
			font-size: 18px; }

		&:last-child {
			margin: 0; } }

	&--footer {

		.b-social__item {
			margin: 0 20px 0 0;
			&:last-child {
				margin: 0; }
			.e-icon {
				font-size: 28px; } } }

	@media screen and (max-width: $sm) {
		&--footer {
			text-align: center;
			margin-bottom: 60px; } } }
