.b-categories-wrap {

	padding-left: calc(1% + 10px);
	//margin-top: 10px
	.tns-inner {
		padding: 20px 0 20px;

		margin-left: 0 !important; }

	.tns-nav {
		display: none; }
	.b-slider-controls__nav.__prev {
		left: calc(-1% - 10px); } }
.b-category {
	display: flex;
	align-items: center;
	//justify-content: center
	flex-direction: column;

	padding: 18px 5px;
	width: 100px;
	height: 95px;
	max-width: 100%;

	background-color: $color-bg-dark;
	@include box-shadow(0, 10px, 14px,0,  rgba(50, 60, 90, 0.1));

	@include transition(all, .4s, ease);
	&:hover,
	&:focus {
		background-color: #ffffff;
		.b-category__figure svg use,
		.b-category__figure svg path {
			fill: $color-bg-dark !important; }
		.b-category__title {
				color: $color-bg-dark; } }
	&__figure {
		margin-bottom: 8px;
		svg {
			max-width: 24px;
			height: 24px;
			@include transition(all, .4s, ease); } }
	&__title {
		font-size: 12px;
		color: #ffffff;
		font-weight: 400;
		text-align: center; } }

@media screen and (min-width: $md) {
	.b-category {
		width: 104px;
		height: 100px;
		padding: 24px 5px; } }

@media screen and (min-width: $lg) {
	.b-categories-wrap {
		margin: 25px 0 30px;
		padding-right: calc(4.25% + 10px);
		padding-left: calc(4.25% + 10px);
		.b-slider-controls {
			display: none; } }

	.b-category {
		padding: 40px 20px;
		width: 171px;
		height: 163px;
		&__figure {
			margin-bottom: 20px;

			svg {
				max-width: 48px;
				height: auto;
				min-height: 47px; } }
		&__title {
			font-size: 14px;
			font-weight: 500; } } }
