.b-logo {
	display: block;
	position: relative;
	z-index: 6;
	&__img {
		width: 110px;
		height: 35px; }


	&__title {
		display: none;
		color: $color-font-light; }

	//.b-logo
	@media screen and (min-width: $lg) {
		&__img {
			width: 141px;
			height: 45px;
			margin-bottom: 4px;
			&--footer {
				width: 110px;
				height: 35px;
				margin-bottom: 0; } }
		&__title {
			font-size: 10px;
			display: block;
			margin-left: 66px; } }

	@media screen and (min-width: $xl) {
		&__img {
			width: 166px;
			height: 53px;
			&--footer {
				width: 110px;
				height: 35px;
				margin-bottom: 0; } }

		&__title {
			font-size: 14px;
			margin-left: 74px; } } }

