.b-pagination {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 25px 0;
	&--on-tablet {
		display: none; }
	&__prev {
		margin-right: 12px; }
	&__next {
		margin-left: 12px; }
	&__prev,
	&__next {
		display: flex;
		align-items: center;
		justify-content: center;

		width: 37px;
		height: 37px;
		background-color: #ffffff;

		@include box-shadow(0,6px,20px,0, rgba(133, 145, 176, 0.31));
		@include border-radius(50%);
		@include transition(all, .4s, ease);
		svg {
			color: $accent; }
		&:hover,
		&:focus {

			background-color: $accent;
			svg {
				color: #ffffff; } } }
	&__item {
		display: block;
		margin: 0 12px;
		font-size: 18px;
		font-weight: 500;
		color: $color-font-dark;

		&.active {
			color: $accent;
			text-decoration: underline; }
		&:hover,
		&:focus {
			color: $accent; } }

	@media screen and (min-width: $sm) {
		&--on-tablet {
			display: flex; } }
	@media screen and (min-width: $lg) {
		&--on-tablet {
			display: none; } } }
