.b-search-options {
	margin: 10px 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	&--table {
		display: none; }
	&__caption {
		font-size: 20px;
		color: #ffffff;
		margin-right: 7px; }
	&__item {
		margin-right: 7px; }
	//.b-search-options
	@media screen and (max-width: $max-md) {
		&__caption {
			width: 100%;
			margin-bottom: 25px; }
		&__btn {
			border-color: $dark;
			color: $dark; } }

	@media screen and (min-width: $sm) {
		&--table {
			display: flex; } }
	@media screen and (min-width: $md) {
		&__caption {
			color: #49505a; } }
	@media screen and (min-width: $lg) {
		&--table {
			display: none; } } }
