.b-articles-list {
	margin-bottom: 60px;
	&__item {
		flex: 0 0 100%;
		max-width: 100%;

		padding: 10px 30px; }

	//.b-articles-list
	@media (min-width: $sm) and (max-width: $max-lg) {
		display: none; }
	@media screen and (min-width: $lg) {
		&__item {
			flex: 0 0 33.333%;
			max-width: 33.333%; } } }
