.b-coupon-list {
	margin-bottom: 30px;

	&__item {
		flex: 0 0 100%;
		max-width: 100%;
		padding: 10px;

		&--middle {
			display: flex;
			align-items: center; }

		&--desctop {
			display: none; } }

	//b-coupon-list
	@media screen and (min-width: $sm) {
		&__item {
			flex: 0 0 50%;
			max-width: 50%;
			&--mobile {
				display: none; } } }
	@media screen and (min-width: $lg) {
		margin-bottom: 60px;
		&__item {
			flex: 0 0 33.333%;
			max-width: 33.333%;
			&--desctop {
				display: block;
				&.b-coupon-list__item--middle {
					display: flex; } } } } }
