.e-button {
  position: relative;
  z-index: 1;
  display: inline-block;
  font: 400 12px/1.2 "Roboto", Helvetica, Arial, sans-serif;
  color: #ffffff;
  padding: 5px 16px 7px;
  border: 1px solid transparent;
  text-align: center;
  border-radius: 1.8em;
  -webkit-border-radius: 1.8em;
  -moz-border-radius: 1.8em;
}

.e-button--transp {
  border-color: #ffffff;
}

.e-button--dark {
