.e-button {
	position: relative;
	z-index: 1;
	display: inline-block;
	font: 400 12px/1.2 $font-defoult;

	color: #ffffff;
	padding: 5px 16px 7px;
	border: 1px solid transparent;

	text-align: center;

	@include border-radius(1.8em);

	&--transp {
		border-color: #ffffff; }
	&--dark {
		color: $dark;
		border-color: $dark; }
	&--add-accent {
		border-color: transparent;
		background-color: $addition-color; }
	&:before {
		content: '';
		position: absolute;
		top: -1px;
		right: -1px;
		bottom: -1px;
		left: 50%;
		background-color: $accent;
		z-index: -1;
		width: 0;
		@include border-radius(1.8em);
		@include transition(all, .4s, ease); }

	&:hover,
	&:focus {
		color: #ffffff;
		border-color: transparent;
		&:before {
			left: -1px;
			width: calc(100% + 2px); } }




	//.e-button
	@media screen and (min-width: $lg) {
		font-size: 16px;
		padding: 5px 26px 7px;
		min-width: 120px; }
	@media screen and (min-width: $xl) {
		min-width: 143px; } }

.b-buton {

	&--right {
		text-align: right; } }
