.b-article {

	&__date {
		font-size: 14px;
		display: inline-block;
		margin-bottom: 16px;
		color: $color-silver;
		padding-bottom: 5px;
		border-bottom: 1px solid $color-silver; }

	&__title {
		font: 16px $font-addition; }

	&__preview {
		margin-bottom: 15px;
		max-width: 232px; }

	&__text {
		margin-bottom: 20px; }

	&__more {
		position: relative;
		color: $color-blue-light;
		text-decoration: underline;

		&:after {
			content: '';
			display: inline-block;
			width: 13px;
			height: 8px;
			background: transparent url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABoAAAAQCAMAAAA/D5+aAAAAJ1BMVEUAAABllbpklrpgj79llbpklbpklbpklbpklbpllrpkk7tllrpklbrAOFgcAAAADXRSTlMAML8QYP/P8O/QQD/AeKO4AwAAAEpJREFUeAGdzTsSgDAMA1EJW/kE7n9eCiYNsTMD275i8Y6HIc6lkhir1Bib9f8mBzDOtfKYsnxHI2jOlqzspPKbsGUCl7ohjteUG1+xBAc+E1oTAAAAAElFTkSuQmCC) center no-repeat;
			background-size: contain;
			margin-left: 10px; }

		&:hover,
		&:focus {
			text-decoration: none; } }
	//
	@media screen and (min-width: $lg) {
		&__date {
			margin-bottom: 24px; }

		&__preview,
		&__text {
			margin-bottom: 30px; } } }
