.b-caption {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 35px;
	&__title {
		font-weight: 400;
		font-size: 20px;
		margin-bottom: 0; }
	&__line {
		margin: 0 5%;
		display: block;
		height: 1px;
		background-color: #a9b1c7;
		flex: 1; }

	//.b-caption
	@media screen and (min-width: $lg) {

		&__title {
			font-size: 28px; }
		&__line {
			margin: 0 3%; } } }
