.b-navigation {

	&__item {
		position: relative;
		font-size: 16px;

		letter-spacing: 0.29px;
		color: #ffffff;
		&:hover,
		&:focus {
			color: $accent-light; }
		&--only-mob {
			display: none; }
		&--tdu {
			text-decoration: underline; }
		&--mb-30 {
			margin-bottom: 30px !important; } }

	//.b-navigation
	@media screen and (max-width: $max-md) {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin: 0 auto;

		.e-button {
			order: -1;
			border: none;
			padding: 0;
			font-size: 16px;
			margin-bottom: 12px;
			&:before {
				display: none; }
			&:hover {
				color: $accent-light; } }
		&__item {
			margin-bottom: 12px;
			&--only-mob {
				display: block; } } }
	@media screen and (min-width: $md) {
		&__item {
			font-size: 12px;
			margin-right: 1em; } }
	@media screen and (min-width: $lg) {
		&__item {
			font-size: 16px;
			&:before {
				content: '';
				display: block;
				position: absolute;
				top: 100%;
				left: 0;
				right: 0;
				height: 1px;
				width: 0;
				background-color: $accent-light;
				@include transition(all, .35s, ease); }
			&:hover,
			&:focus {
				&:before {
					width: 100%; } } } }

	@media screen and (min-width: $xl) {
		&__item {
			margin-right: 2.5em; } } }
