.b-slider {
	position: relative;
	//margin-left: -8px
	//margin-right: -8px
	&__item {
		padding: 3px; } }
.b-slider-controls {
	//display: flex
	//justify-content: space-between
	//z-index: 1
	//position: absolute
	//top: 50%
	//left: 0
	//right: 0
	//+translate(0, -50%)

	&__nav {
		position: absolute;
		top: 50%;
		left: 0;

		z-index: 1;
		display: flex;
		align-items: center;
		justify-content: center;

		width: 37px;
		height: 37px;
		background-color: #ffffff;

		@include box-shadow(0,6px,20px,0, rgba(133, 145, 176, 0.31));
		@include border-radius(50%);
		@include transition(all, .4s, ease);
		@include translate(0, -50%);
		svg {
			color: $accent; }
		&.__next {
			right: 0;
			left: auto; }

		&:hover,
		&:focus {

			background-color: $accent;
			svg {
				color: #ffffff; } } } }

@media screen and (min-width: $lg) {
	.b-slider {
		&__item {
			padding: 8px; } } }
